<template>
    <div>
        <TopLink></TopLink>
        <img src="images/timg.gif" v-if="mlArr.length==0" class="loding">
        <div v-else>
            <div class="clear"></div>
            <MovieList title="影院热映" :mlData="mlArra" :url="url"></MovieList>
            <MovieList title="免费在线观影" :mlData="mlArrb" :url="url"></MovieList>
            <MovieList title="发现好电影" :mlData="mlArrc" :url="url"></MovieList>
        </div>
        <Fxhdy title="发现好电影"></Fxhdy>
        <Sort title="分类浏览" :sorts="sorts"></Sort>
    </div>
</template>
<script>
import TopLink from "@/components/toplink/toplink.vue"
import MovieList from "@/components/movieList/movieList.vue"
import Fxhdy from "@/pages/movie/fxhdy/fxhdy.vue"
import Sort from "@/pages/movie/sort/sort.vue"
export default {
    components:{
        TopLink,MovieList,Fxhdy,Sort
    },
    data(){
        return {
            mlArr:[],
            sorts:[],
            url:"/xiangqing"
        }
    },
    mounted() {
        this.axios({
            url:"/api/movie/movieList",
            method:"get",
        }).then((res)=>{
            this.mlArr = res.data.data;
            this.mlArr.forEach(v=>{
                v.rating.average = v.rating.average/2
            });
        });
        this.axios({
            url:"/api/movie/sorts",
            method:"get",
        }).then((res)=>{
            this.sorts = res.data.data;
        });
    },
    computed:{
        mlArra(){
            return this.mlArr.slice(0,6)
        },
        mlArrb(){
            return this.mlArr.slice(6,12)
        },
        mlArrc(){
            return this.mlArr.slice(12,18)
        },
    },
}
</script>
<style scoped>
    .loding{
        width: 100%;
    }
    .clear{
        width: 100%;
        height: 0.4rem;
    }
</style>